<template>
    <div>
        <div class="drag" :style="style" >
            <div class="background"/>
            <div class="text shadow" onselectstart="return false" :style="{ color: textColor }">
            <slot name="content">
                {{ content }}
            </slot>
            </div>
            <div class="slider" :style="{height,width:sliderWidth}">
                <slot v-if="icon" name="icon">
                    <i :class="icon"></i>
                </slot>
                <slot v-else name="icon">
                    >>
                </slot>
            </div>
        </div>
    </div>
</template>
   
<script>
const debounce = (function () {
    let timer = 0
    return function (callback, ms) {
        clearTimeout(timer)
        timer = setTimeout(callback, ms)
    }
})();

export default {
    name: 'SliderVerify',
    model: {
        event: 'change',
        prop: 'isLock'
    },
    props: {
        isLock: { //
            type: [String, Boolean, Number, Object],
            required: true,
            default: false
        },
        icon: { //
            type: [String],
            default: "el-icon-d-arrow-right"
        },
        activeValue: { //
            type: [String, Boolean, Number, Object],
            default: true
        },
        inactiveValue: { //
            type: [String, Boolean, Number, Object],
            default: false
        },
        content: { //
            type: [String],
            default: ""
        },
        height: { //
            type: [String],
            default: "40px"
        },
        sliderWidth: { //
            type: [String],
            default: "40px"
        },
        background: { //
            type: [String],
            default: "#e8e8e8"
        },
        textColor: { //
            type: [String],
            default: "#777"
        }
    },
    watch: {
        isLock(data) { //
            !data && this.init();
        },
    },
    computed: {
        style() {
            const {height, background} = this;
            return {height, 'line-height': height, background};
        },
        resize() {
            return document.body.clientWidth;
        },
    },
    mounted() {
        this.init();
        window.onresize = () => {
            debounce(() => {
                this.init();
            }, 120);
        };
    },
    methods: {
        /**
            * DOM-
            */
        selector(selector) {
            return document.querySelector(selector);
        },
        /**
            * 
            */
        init() {
            const box = this.selector('.drag'); //
            if(box) {
                const background = this.selector('.background'); //
                const text = this.selector('.text'); //
                const slider = this.selector('.slider');//
                const distance = box.offsetWidth - slider.offsetWidth;//
                let success = this.inactiveValue;//
                //  
                slider.style.transition = null;
                background.style.transition = null;
                slider.style.left = 0 + 'px';
                background.style.width = 0 + 'px';
                text.innerHTML = this.content;
                slider.innerHTML = '<i class="el-icon-d-arrow-right"></i>';
                slider.style.color = '#3fcd26';
                //
                slider.onmousedown = (event) => {
                    //1.
                    slider.style.transition = null;
                    background.style.transition = null;
                    //clientX ()
                    //2.
                    const ev = event || window.event;
                    const downX = ev.clientX;
                    //
                    document.onmousemove = (e) => {
                        const evt = e || window.event;//IEieie,window.event,ieeventvar e = e||window.event
                        //1.
                        const moveX = evt.clientX;
                        //2. - 
                        let offsetX = moveX - downX;
                        //3.   
                        if (offsetX > distance) {
                            offsetX = distance;//
                        } else if (offsetX < 0) {
                            offsetX = 0;//
                        }
                        //4.
                        slider.style.left = offsetX + 'px';
                        background.style.width = offsetX + 'px';
                        // = 
                        if (offsetX == distance) {
                            //1.
                            text.innerHTML = '';
                            text.style.color = '#53C300';
                            slider.innerHTML = '<i class="el-icon-success"></i>';
                            slider.style.color = '#53C300';
                            //2.
                            success = this.activeValue;
                            //
                            slider.onmousedown = null;
                            document.onmousemove = null;
                            //3.
                            setTimeout(() => {
                                this.$emit('change', this.activeValue);
                            }, 100);
                        }
                    };
                    //
                    document.onmouseup = () => {
                        //
                        if (success == this.activeValue) return true;
                        //1s
                        slider.style.left = 0;
                        background.style.width = 0;
                        slider.style.transition = 'left 1s ease';
                        background.style.transition = 'width 1s ease';
                        //
                        document.onmousemove = null;
                        document.onmouseup = null;
                    };
                };
                /*  */
                //
                slider.ontouchstart = (event) => {
                    const touch = event.changedTouches[0];
                    //1.
                    slider.style.transition = null;
                    background.style.transition = null;
                    //clientX ()
                    //2.
                    const downX = touch.pageX;
                    //
                    document.ontouchmove = (e) => {
                        const tev = e.changedTouches[0];
                        //1.
                        const moveX = tev.pageX;
                        //2. - 
                        let offsetX = moveX - downX;
                        //3.   
                        if (offsetX > distance) {
                            offsetX = distance;//
                        } else if (offsetX < 0) {
                            offsetX = 0;//
                        }
                        //4.
                        slider.style.left = offsetX + 'px';
                        background.style.width = offsetX + 'px';
                        // = 
                        if (offsetX == distance) {
                            //1.
                            text.innerHTML = '';
                            text.style.color = '#53C300';
                            slider.innerHTML = '&radic;';
                            slider.style.color = '#53C300';
                            //2.
                            success = this.activeValue;
                            //
                            slider.ontouchstart = null;
                            document.ontouchmove = null;
                            //3.
                            setTimeout(() => {
                                this.$emit('change', this.activeValue);
                                // console.log('');
                            }, 100);
                        }
                    };
                    //
                    document.ontouchend = () => {
                        //
                        if (success == this.activeValue) return true;
                        //1s
                        slider.style.left = 0;
                        background.style.width = 0;
                        slider.style.transition = 'left 1s ease';
                        background.style.transition = 'width 1s ease';
                        //
                        document.ontouchmove = null;
                        document.ontouchend = null;
                    };
                };
            }
        }
    }
};
</script>

<style scoped>
* {
    margin: 0px;
    padding: 0px;
    font-family: "";
    box-sizing: border-box;
}

.drag {
    height: 2.5rem;
    line-height: 2.5rem;
    background-color: #e8e8e8;
    position: relative;
    margin: 0 auto;
    border-radius: 3px;
}

.background {
    width: 2.5rem;
    height: 100%;
    position: absolute;
    background-color: #53C300;
    border-radius: 3px 0 0 3px;
}

.text {
    position: absolute;
    width: 100%;
    height: 100%;
    text-align: center;
    user-select: none;
}

.slider {
    width: 2.5rem;
    height: 2.375rem;
    position: absolute;
    border: 1px solid #ccc;
    cursor: move;
    font-family: "";
    text-align: center;
    background-color: #fff;
    user-select: none;
    color: #666;
}

.shadow {
    text-align: center;
    background: -webkit-gradient(linear, left top, right top, color-stop(0, #4d4d4d), color-stop(.2, #5d5d5d),
    color-stop(.4, #6d6d6d), color-stop(.5, white), color-stop(.6, #6d6d6d), color-stop(.8, #5d5d5d), color-stop(1, #4d4d4d));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-animation: animate 3s infinite;
}

@-webkit-keyframes animate {
    from {
    background-position: -80px;
    }
    to {
    background-position: 80px;
    }
}

@keyframes animate {
    from {
    background-position: -80px;
    }
    to {
    background-position: 80px;
    }
}
</style>