import { createRouter, createWebHistory } from 'vue-router'
import PkIndexView from '@/views/pk/PkIndexView'
import RankListIndexView from '@/views/ranklist/RankListIndexView'
import RecordIndexView from '@/views/record/RecordIndexView'
import RecordContentView from '@/views/record/RecordContentView'
import DiscussionView from '@/views/discussion/DiscussionView'
import FreshNewsView from '@/views/fresh_news/FreshNewsView'
import MessageBoardView from '@/views/message_board/MessageBoardView'
import UserBotIndexView from '@/views/user/bots/UserBotIndexView'
import NotFound from '@/views/error/NotFound'
import UserAccountLoginView from '@/views/user/account/UserAccountLoginView'
import UserAccountRegisterView from '@/views/user/account/UserAccountRegisterView'
import UserAccountAcWingWebReceiveCodeView from '@/views/user/account/UserAccountAcWingWebReceiveCodeView'
import UserAccountQQReceiveCodeView from '@/views/user/account/UserAccountQQReceiveCodeView'
import store from '@/store'

const routes = [
    {
        path: '/',
        name: 'home',
        redirect: '/record/',
        meta: {
            requestAuth: true,
            description: '',
            isRoot: true,
        }
    },
    {
        path: '/pk/:game/',
        name: 'pk_index',
        component: PkIndexView,
        meta: {
            requestAuth: true,
            description: '',
            isRoot: false,
        }
    },
    {
        path: '/record/',
        name: 'record_index',
        component: RecordIndexView,
        meta: {
            requestAuth: true,
            description: '',
            isRoot: true,
        }
    },
    {
        path: "/record/:recordId/",
        name: "record_content",
        component: RecordContentView,
        meta: {
            requestAuth: true,
            description: '',
            isRoot: false,
        }
    },
    {
        path: '/ranklist/',
        name: 'ranklist_index',
        component: RankListIndexView,
        meta: {
            requestAuth: true,
            description: '',
            isRoot: true,
        }
    },
    {
        path: '/discussion/',
        name: 'discussion_index',
        component: DiscussionView,
        meta: {
            requestAuth: true,
            description: '',
            isRoot: true,
        }
    },
    {
        path: '/fresh_news/',
        name: 'fresh_news_index',
        component: FreshNewsView,
        meta: {
            requestAuth: true,
            description: '',
            isRoot: true,
        }
    },
    {
        path: '/message_board/',
        name: 'message_board_index',
        component: MessageBoardView,
        meta: {
            requestAuth: true,
            description: '',
            isRoot: true,
        }
    },
    {
        path: '/user/bot/',
        name: 'user_bot_index',
        component: UserBotIndexView,
        meta: {
            requestAuth: true,
            description: 'Bot',
            isRoot: true,
        }
    },
    {
        path: '/user/account/login/',
        name: 'user_account_login',
        component: UserAccountLoginView,
        meta: {
            requestAuth: false,
            description: '',
            isRoot: true,
        }
    },
    {
        path: '/user/account/register/',
        name: 'user_account_register',
        component: UserAccountRegisterView,
        meta: {
            requestAuth: false,
            description: '',
            isRoot: true,
        }
    },
    {
        path: "/player/acwing/web/receive_code/",
        name: 'player_acwing_web_receive_code',
        component: UserAccountAcWingWebReceiveCodeView,
        meta: {
            requestAuth: false,
            description: '',
            isRoot: true,
        }
    },
    {
        path: "/player/qq/receive_code/",
        name: 'player_qq_receive_code',
        component: UserAccountQQReceiveCodeView,
        meta: {
            requestAuth: false,
            description: '',
            isRoot: true,
        }
    },
    {
        path: '/404/',
        name: '404',
        component: NotFound,
        meta: {
            requestAuth: false,
            description: '',
            isRoot: true,
        }
    },
    {
        path: '/:catchAll(.*)',
        redirect: '/404/',
    },
]

const router = createRouter({
    history: createWebHistory(),
    routes
})

router.beforeEach((to, from, next) => {
    if(to.meta.isRoot) store.commit("updateBackPage", "");
    if(to.meta.requestAuth && !store.state.user.is_login) {
        next({name: "user_account_login"});
    } else {
        next();
    }
})

export default router
